import { ActivatedRouteSnapshot, Resolve } from '@angular/router';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { DetailService } from '@app/core/http/detail.service';

@Injectable()
export class UserProfileReslover implements Resolve<any> {

    constructor(private question: DetailService) {
    }

    resolve(route: ActivatedRouteSnapshot):
        Observable<any> {
      
        const routeUrl = `user-profile`;

        return this.question.getDetail(routeUrl);
    }
}